.product {
  background-color: #fafafa;
  padding-top: 70px;
  padding-bottom: 70px;
  overflow: hidden;

  &_headline {

    .h2-dark {
      color: #333;

      span {
        display: block;
      }
    }
  }

  &_wrap {
    max-width: 1109px;
    margin: 0 auto;
    min-height: 1058px;
    position: relative;
    z-index: 0;
  }

  &_cards {
    padding-top: 50px;
  }

  &_item {
    margin-bottom: 30px;
    position: relative;

    &:last-child {
      margin-bottom: 0;
    }

    &:after {
      content: '';
      position: absolute;
      display: block;
      z-index: 1;
      background-repeat: no-repeat;
    }

    &--five:after {
      top: 13px;
      left: 208px;
      @include size(495.093px, 55.796px);
      background-image: svg-load('five.svg');
    }

    &--four:after {
      top: 13px;
      left: 208px;
      @include size(501.699px, 157.448px);
      background-image: svg-load('four.svg');
    }

    &--three:after {
      top: 13px;
      left: 208px;
      @include size(130.774px, 164.949px);
      background-image: svg-load('three.svg');
    }

    &--two:after {
      top: 13px;
      left: 208px;
      @include size(176.142px, 180.372px);
      background-image: svg-load('two.svg');
    }

    &--one:after {
      top: 13px;
      left: 208px;
      @include size(264.926px, 448.009px);
      background-image: svg-load('one.svg');
    }
  }

  &_box {
    margin-left: -60px;
    max-width: 240px;
  }

  &_header {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
  }

  &_icon {
    min-width: 60px;
    width: 60px;
    height: 60px;
    background-color: #fff;
    border: 4px solid #2E377C;
    border-radius: 50%;
    margin-right: -20px;
    z-index: 1;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
  }

  &_name {
    min-width: 229px;
    background-color: #273481;
    color: #FD0;
    font-family: 'PF Agora Slab Pro';
    font-size: 15px;
    height: 34px;
    line-height: 15px;
    padding: 10px 20px 10px 30px;
    position: relative;

    &:before {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 0 34px 20px;
      border-color: transparent transparent #fafafa transparent;
    }
  }

  &_body {
    padding-left: 60px;
    font-family: 'Open Sans';
    font-size: 8px;
    line-height: 10px;
    color: #333333;

    p { margin: 0; }

    &_info {
      position: relative;
      width: 400px;
      margin-top: 30px;
      /* width: 100%; */
      display: flex;
      align-items: flex-start;
      align-content: flex-start;

      &_box {
        display: flex;
        align-items: flex-start;
        align-content: flex-start;
      }

      .img-versimax {
        width: 168px;
        min-width: 168px;
      }

      .info-versimax {
        min-width: 38px;
        max-width: 34px;
        color: #2E377C;
        font-size: 5px;
        line-height: 8.8px;
        margin-left: 15px;
        margin-right: 15px;
        position: relative;

        &:after {
          content: '';
          position: absolute;
          @include size(24.087px, 10.646px);
          background-image: svg-load('arow.svg');
          z-index: 1;
          background-repeat: no-repeat;
          bottom: -12px;
          right: -16px;
        }
      }

      .qr-versimax {
        min-width: 38px;
        max-width: 38px;
      }
    }
  }

  &_img {
    z-index: -1;
    position: absolute;
    bottom: 0;
    top: 115px;
    right: -165px;
    width: 1058px;
    height: 1058px;
    margin: 0;
    background-attachment: local;
    background-position: 0% 0%;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
