.specifications {
  background-color: #273481;
  padding-top: 80px;
  padding-bottom: 60px;
  position: relative;
  display: block;
  visibility: visible;
  opacity: 1;

  &_wrap {
    max-width: 1109px;
    margin: 0 auto;
  }

  &_badge {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 354px;
    height: 354px;
    border-radius: 50%;
    background-color: #ffffff;
    position: absolute;
    right: calc(354px / 2);
    top: calc(-354px / 2);
    z-index: 1;

    img {
      vertical-align: bottom;
    }
  }

  &_headline {
    padding-bottom: 30px;
    border-bottom: 3px solid #FFDD00;

    h2 {

      @media #{$lg} {
        max-width: 500px;
      }
    }
  }

  &_tabs {
    display: flex;
    justify-content: center;
    margin-top: 30px;

    &_panel {
      margin-top: 70px;
      margin-bottom: 40px;

      &-content {
        display: flex;
        align-items: center;
        align-content: center;
        flex-direction: column;
        justify-content: center;

        img {
          text-align: center;
          vertical-align: bottom;
        }
      }
    }
  }
}
