.footer {
  background-color: #273481;
  padding: 60px 0;

  &_mobile {
    display: none;
    visibility: hidden;
    opacity: 0;
  }

  &_wrap {
    max-width: 1109px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: space-between;
  }

  &_copyright {
    color: #FFFFFF;
    font-family: $font-family-roboto;
    font-size: 26px;
    line-height: 26px;

    p:last-child { margin-bottom: 0; }
  }
}

.logo-link:hover {

  svg {
    transition: all .3s ease;
    position: relative;
    top: -1px;
  }
}

.logo-link-right:hover {

  svg {
    transition: all .3s ease;
    position: relative;
    top: -1px;
  }
}
